:root {
  --background: #fafafa;
  --foreground: #171717;
  --frybix-purple: #5B4B8A;
  --frybix-purple-dark: #4A3D72;
  --frybix-purple-light: #7B6BA8;
  --frybix-blue: #4F7CAC;
  --frybix-highlight: #F5E6C8;
  --frybix-dots: #5B4B8A;
}
html {
  scroll-behavior: smooth;
}
body {
  background: #f8f7fc;
  color: var(--foreground);
  font-family: var(--font-sans), system-ui, sans-serif;
}
.dot-pattern {
  background-color: #f8f7fc;
  background-image: radial-gradient(circle, rgba(91, 75, 138, 0.15) 1px, transparent 1px);
  background-size: 24px 24px;
}
@keyframes trusted-by-scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
.animate-trusted-by-scroll {
  animation: trusted-by-scroll 35s linear infinite;
  will-change: transform;
}
@keyframes fade-right {
  from {
    opacity: 0;
    transform: translateX(32px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.animate-fade-right {
  animation: fade-right 0.7s ease-out forwards;
}
@keyframes fade-left {
  from {
    opacity: 0;
    transform: translateX(-32px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.animate-fade-left {
  animation: fade-left 0.7s ease-out forwards;
}
@keyframes fade-up {
  from {
    opacity: 0;
    transform: translateY(24px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-up {
  animation: fade-up 0.7s ease-out forwards;
}
@keyframes fade-up-on-view {
  from {
    opacity: 0;
    transform: translateY(48px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-up-on-view {
  animation: fade-up-on-view 1s ease-out forwards;
}
@keyframes fade-down {
  from {
    opacity: 0;
    transform: translateY(-48px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-down {
  animation: fade-down 1s ease-out forwards;
}
@keyframes zoom-in {
  from {
    opacity: 0;
    transform: scale(0.88);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-zoom-in {
  animation: zoom-in 0.9s ease-out forwards;
  transform-origin: center center;
}
@keyframes zoom-out {
  from {
    opacity: 0;
    transform: scale(1.15);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-zoom-out {
  animation: zoom-out 0.9s ease-out forwards;
  transform-origin: center center;
}
@keyframes zoom-out-up {
  from {
    opacity: 0;
    transform: scale(1.12) translateY(-24px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
.animate-zoom-out-up {
  animation: zoom-out-up 0.9s ease-out forwards;
  transform-origin: center center;
}
@keyframes flip-left {
  from {
    opacity: 0;
    transform: perspective(800px) rotateY(-50deg);
  }
  to {
    opacity: 1;
    transform: perspective(800px) rotateY(0);
  }
}
